@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: "Lato";
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.main-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50rem;
    /* background-color: #241023; */
    /* border: 1px solid #241023; */
    border-radius: 1rem;
}

.heading {
    background-color: #241023;
    color: #FAFAFF;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
}


.heading p {
    font-size: 4rem;
}

.content-addition {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    margin: 2rem 0;
}

#take-input {
    border: none;
    font-size: 3rem;
    /* border: none; */
    outline: none;
    /* border: 1px solid #241023; */
    box-shadow: 0 0.2rem 0 #241023;
    border-radius: 1rem;
    text-align: center;
    color: #241023;
}

#add-button {
    font-size: 3rem;
    background-color: #241023;
    color: #FAFAFF;
    padding: 0.8rem;
    cursor: pointer;
    border-radius: 1rem;
}

.display-items {
    margin: 1rem 0;
    width: 100%;
}

.single-item {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    width: 100%;
    margin: 1rem 0;
}

.single-item p {
    width: 60%;
    border-radius: 1rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
    text-align: center;
    background: #241023;
    color: #FAFAFF;
}

#delete-item, #completed {
    font-size: 2rem;
    background-color: #241023;
    color: #FAFAFF;
    padding: 0.8rem;
    cursor: pointer;
    border-radius: 1rem;
}